import { merge } from '@syncfusion/ej2-base';
/**
 * Row
 * @hidden
 */
export class Row {
    constructor(options) {
        merge(this, options);
    }
    clone() {
        let row = new Row({});
        merge(row, this);
        row.cells = this.cells.map((cell) => cell.clone());
        return row;
    }
}
